.footer {
    display: flex;
    flex-direction: column;
    align-items: center;
}
.footerContainer {
    background-color: #c0bdbd;
    border: 1px solid;
    width: 320px;
    display: flex;
    justify-content: space-between;
}
.icon {
    background-color: dimgrey;
    width:45px;
    height:45px;
}
.name{
    font-family: Raleway-Black, sans-serif;
    font-size: 20px;
    margin-top: 10px;
    margin-bottom: 10px;
}
.coperights{
    font-family: Raleway-regular, sans-serif;
    font-size: 17px;
    margin-top: 10px;
    margin-bottom: 10px;
}